<template>
  <div>
    <van-nav-bar class="page-nav-bar" title="搜索">
      <template #title>
        <van-button to="/search" class="seachbtn" type="info">
          <template #icon>
            <i class="toutiao toutiao-sousuo"></i>
          </template>
          <template #default> 搜索 </template>
        </van-button>
      </template>
    </van-nav-bar>
    <van-tabs v-model="active" class="channels">
      <!-- 循环遍历频道列表数据 -->
      <!-- 口诀：有id用id，没id用索引 -->
      <van-tab v-for="item in userChannels" :title="item.name" :key="item.id">
        <art-list :channelsItem="item" />
      </van-tab>
      <div class="hamburgerbtn" @click="hamburgerFn">
        <span class="toutiao toutiao-gengduo"></span>
      </div>
    </van-tabs>
    <van-popup v-model="show" position="bottom" :style="{ height: '100%' }">
      <channelEdit
        @close="show = false"
        :userChannels="userChannels"
        :channelActive="active"
        @ischannel="ischannel"
      />
    </van-popup>
  </div>
</template>
<script>
import { userChannelApi } from "@/api/home";
import ArtList from "./component/artList";
import channelEdit from "./component/channelEdit.vue";
import { getItem } from "@/utils/storage";
export default {
  components: {
    ArtList,
    channelEdit,
  },
  data() {
    return {
      userChannels: [], //用户的频道
      show: false, //调式频道组件，暂时使用true开启，开发完毕了，再改成false即可
      active: 0, //激活标签的索引值
    };
  },
  async created() {
    try {
      if (this.$store.state.token) {
        //用户的频道接口
        let { data } = await userChannelApi();
        this.userChannels = data.data.channels;
      } else {
        //我们先以 MYCHANNEL 作为键来读取缓存，后面我们存缓存的时候也以这个键来存
        let storageChannelList = getItem("MYCHANNEL");
        //判断缓存中是否有我的频道数据
        if (storageChannelList) {
          //如果有，直接把缓存的数据赋值给我的频道
          this.userChannels = storageChannelList;
        } else {
          //用户没登录，缓存也没数据，那就直接请求接口，获取游客数据
          let { data } = await userChannelApi();
          this.userChannels = data.data.channels;
        }
      }
      // console.log(data);
    } catch (error) {
      console.log(error);
    }
  },
  methods: {
    //汉堡按钮点击事件
    hamburgerFn() {
      this.show = true;
    },
    //更新高亮的索引，并关闭弹窗
    ischannel(index, bool) {
      this.show = bool;
      this.active = index;
    },
  },
};
</script>
