.seachbtn {
  width: 555px;
  height: 64px;
  background-color: #5babfb;
  border-radius: 32px;
  .toutiao {
    font-size: 32px;
  }
}
.page-nav-bar {
  //思考：我们加了样式vant组件不生效？
  //原因：用了scoped属性，样式只作用于当前vue文件
  //目标：我们当前style标签内的样式想要影响到vant深层次的dom元素
  //解决：使用deep深度选择符

  // 深度选择符
  //当你使用了第三方组件，修改样式改不了，就需要用到/deep/深度选择符
  /deep/ .van-button__text {
    color: #fff;
  }
  /deep/ .van-nav-bar__title {
    max-width: 100%;
  }
}
.channels {
  // padding-right: 66px;
  /deep/ .van-tab {
    min-width: 190px;
    border-right: 2px solid #edeff3;
  }
  /deep/ .van-tab__text {
    font-size: 30px;
  }
  /deep/ .van-tabs__line {
    width: 31px;
    height: 6px;
    background-color: #3296fa;
    border-radius: 3px;
  }
}
.hamburgerbtn {
  position: absolute;
  right: 0px;
  top: 0px;
  width: 66px;
  height: 82px;
  line-height: 80px;
  text-align: center;
  background: #ffffff;
  opacity: 0.5;
