<template>
  <div>
    <div class="close" @click="$emit('close')">
      <span class="toutiao toutiao-guanbi"></span>
    </div>
    <!-- 我的频道 -->
    <div class="mycha">
      <div>我的频道</div>
      <van-button
        class="editbtn"
        plain
        @click="isEdit = !isEdit"
        type="danger"
        round
      >
        {{ isEdit ? "完成" : "编辑" }}
      </van-button>
    </div>
    <van-grid :gutter="10">
      <van-grid-item
        class="grid-item"
        :class="{ red: channelActive == index }"
        v-for="(item, index) in userChannels"
        :key="item.id"
        :text="item.name"
        @click="mychannelFn(index)"
      >
        <template #icon v-if="isEdit && item.id != 0">
          <van-icon name="clear" class="clearbtn" />
        </template>
      </van-grid-item>
    </van-grid>
    <!-- 频道推荐 -->
    <div class="mycha">
      <div>频道推荐</div>
    </div>
    <van-grid direction="horizontal" :gutter="10">
      <van-grid-item
        class="grid-item"
        v-for="(item, index) in recommendChannels"
        :key="index"
        :text="item.name"
        icon="plus"
        @click="addchannelFn(item)"
      >
      </van-grid-item>
    </van-grid>
  </div>
</template>
<script>
import { allChannelApi, setMyChannelApi } from "@/api/home";
import { setItem } from "@/utils/storage";
export default {
  props: {
    userChannels: Array,
    channelActive: Number,
  },
  data() {
    return {
      allChannel: [], //所有的频道
      isEdit: false, //编辑状态存储，false：非编辑状态，true：编辑状态
    };
  },
  async created() {
    try {
      //请求所有频道
      let { data } = await allChannelApi();
      console.log(data.data.channels);
      this.allChannel = data.data.channels;
    } catch (error) {
      console.log(error);
    }
  },
  methods: {
    //编辑频道
    mychannelFn(index) {
      if (this.isEdit) {
        //一开始这里爆红了这是eslint的代码格式化校验的问题
        // 在.eslintrc.js里关闭即可
        if (this.userChannels[index].id == 0) {
          return false;
        }
        if (index < this.channelActive) {
          this.$emit("ischannel", this.channelActive - 1, true);
        }
        this.userChannels.splice(index, 1);
        //删除操作
      } else {
        //频道的切换
        // 1、改变父页面的激活状态的索引值
        // 2、子向父通信
        // 3、别忘了关闭弹层
        this.$emit("ischannel", index, false);
      }
    },
    //添加频道
    addchannelFn(item) {
      this.userChannels.push(item);
    },
  },
  computed: {
    //推荐频道
    // recommendChannels() {
    //   const channels = [];
    //   this.allChannels.forEach((channel) => {
    //     // find 遍历数组，找到满足条件的元素项（重合的元素）
    //     const ret = this.myChannels.find((myChannel) => {
    //       return myChannel.id === channel.id;
    //     });
    //     // 如果我的频道中不包括该频道项，则收集到推荐频道中
    //     if (!ret) {
    //       channels.push(channel);
    //     }
    //   });
    //   // 把计算结果返回
    //   return channels;
    // },
    recommendChannels() {
      // 这里的 return 是将处理好的数据给计算属性 recommendChannels 进行一个返回
      return this.allChannel.filter((item) => {
        //返回符合条件的第一个 item 元素
        return !this.userChannels.find((it) => {
          //返回符合条件的 it 的元素
          return it.id == item.id;
        });
      });
      // return 所有频道 - 我的频道;
      // 1、find只会返回符合条件的第一个元素
      // 2、find第一次：推荐    filter第一次：推荐
      // 总结：1、利用find找到重叠的元素，
      // 总结：2、利用 ！取反找到不重叠的元素
      // 总结：3、计算属性，需要return返回值
    },
  },
  watch: {
    //监听我的频道数据变化
    userChannels: {
      deep: true,
      handler(newVal) {
        //根据登录状态判断
        if (this.$store.state.token) {
          //走接口
          let arr = [];
          for (let i = 0; i < newVal.length; i++) {
            let obj = {};
            // 处理频道id
            obj.id = newVal[i].id;
            // 处理频道索引
            obj.seq = i;
            arr.push(obj);
          }
          //登录了，调用接口存储频道数据
          setMyChannelApi(arr);
        } else {
          //走缓存，存储我的频道
          setItem("MYCHANNEL", newVal);
        }
      },
    },
  },
};
</script>
