//首页相关的接口都在这个文件下统一封装

import request from "@/utils/request";

//用户选择的频道
export const userChannelApi = () =>
  request({
    url: "/v1_0/user/channels",
    method: "get",
  });

//请求文章列表
export const articleListApi = (params) =>
  request({
    url: "/v1_0/articles",
    method: "get",
    params,
  });

//所有频道接口
export const allChannelApi = () =>
  request({
    url: "/v1_0/channels",
    method: "get",
  });

//设置频道列表接口
export const setMyChannelApi = (channels) =>
  request({
    url: "/v1_0/user/channels",
    method: "put",
    data: {
      channels,
    },
  });

//获取搜索联想词接口
export const searchSuggestionApi = (val) =>
  request({
    url: "/v1_0/suggestion",
    method: "get",
    params: {
      q: val,
    },
  });

//获取搜索结果接口
export const searchResultApi = (obj) =>
  request({
    url: "/v1_0/search",
    method: "get",
    params: obj,
  });
