<template>
  <div>
    <div class="artlistcon">
      <!-- 当list组件的第一页数据，没有铺满一屏的时候，会自动加载第二页的数据 -->
      <van-pull-refresh
        :success-text="successText"
        v-model="isLoading"
        @refresh="onRefresh"
      >
        <van-list
          v-model="loading"
          :finished="finished"
          finished-text="没有更多了"
          @load="onLoad"
        >
          <!-- 单个的列表项 -->
          <!-- 封装的是列表项，传递数据的时候，传单个项的数据即可 -->
          <!-- 原因：1、项目当中多次会用到的组件，不一定是同一个接口 -->
          <!-- 2、接口的请求放在列表即可，列表项只是用来承载数据  -->
          <artItem v-for="item in list" :key="item.art_id" :artitem="item" />
          <!-- <van-cell
            v-for="item in list"
            :key="item.art_id"
            :title="item.title"
          /> -->
        </van-list>
      </van-pull-refresh>
    </div>
  </div>
</template>
<script>
import { articleListApi } from "@/api/home";
import artItem from "@/components/artItem";
export default {
  components: {
    artItem,
  },
  props: {
    channelsItem: Object,
  },
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      timestamp: new Date().valueOf(),
      isLoading: false,
      successText: "",
    };
  },
  async created() {
    // try {
    //   let obj = {
    //     channel_id: this.channelsItem.id,
    //     timestamp: new Date().valueOf(),
    //   };
    //   let { data } = await articleListApi(obj);
    //   this.list = data.data.results;
    //   // console.log(list);
    // } catch (error) {
    //   console.log(error);
    // }
  },
  methods: {
    async getArtList() {
      let obj = {
        channel_id: this.channelsItem.id,
        timestamp: this.timestamp,
      };
      let { data } = await articleListApi(obj);
      if (data.data.pre_timestamp == null || data.data.results.length == 0) {
        //当没有数据了，数据已全部加载完毕，则直接将 finished 设置成 true 即可。
        this.finished = true;
      } else {
        //继续加载下一页数据
        //timestamp需要上一次网络请求里pre_timestamp的值, 用于请求下一段数据
        this.timestamp = data.data.pre_timestamp;
        // list能做赋值吗？
        // 直接赋值就覆盖了上一页的数据！
        // 展开运算符讲解
        // list =[1,2,3]
        // results =[4,5,6]
        // this.list =[1,2,3,4,5,6]
        this.list = [...this.list, ...data.data.results];
        // 数据更新完毕后，将 loading 设置成 false
        this.loading = false;
      }
    },
    async onLoad() {
      // 1、页面触底会出发load事件；没有铺满一屏的时候也会出发load事件
      // 2、发起异步操作
      // 3、第二页的数据怎么请求，看接口文档怎么说
      this.getArtList();
    },
    async onRefresh() {
      try {
        //接口请求
        //你都想下拉刷新了，是不是拿最新的数据
        let obj = {
          channel_id: this.channelsItem.id,
          timestamp: new Date().valueOf(),
        };
        let { data } = await articleListApi(obj);
        //最新的数据，是不是要显示到最上面
        //往数组的前面插入数据
        this.list.unshift(...data.data.results);
        //优化：让用户知道更新了几条数据
        this.successText = `刷新成功，刷新了${data.data.results.length}条数据`;
        // 操作完成后将 v-model 设置为 false，表示加载完成
        this.isLoading = false;
      } catch (error) {
        // 操作完成后将 v-model 设置为 false，表示加载完成
        this.isLoading = false;
        // 接口失败了。同样给一个提示，刷新失败
        this.successText = "刷新失败";
        console.log(error);
      }
    },
  },
};
</script>
<style scoped>
.artlistcon {
  position: fixed;
  top: 180px;
  left: 0px;
  right: 0px;
  bottom: 100px;
  overflow-y: auto;
}
</style>
